export default {
  name: 'activityFriendInfo',
  props: {
    scenicInfo: Object,
    assist: Object
  },
  data () {
    return {
    }
  },
  computed: {
    tip () {
      if (this.$userInfo.isLogin()) {
        if (this.assist && this.assist.join.status === 1) {
          return '立即购买'
        }
        return '邀请好友'
      } else {
        return '邀请好友'
      }
    }
  },
  methods: {
    activityRuleInfo () {
      this.$emit('ativityRuleInfo')
    },
    inviteFriend () {
      this.$emit('inviteFriend')
    },
	makePhone () {
		uni.makePhoneCall({
			phoneNumber: this.scenicInfo.tel
		})
	},
    delHtmlTag (str) {
      if (str) {
        return str.replace(/<[^>]+>/g, '')
      }
      return ''
    }
  }
}
